import React from 'react';
import { Panel, ListItem, Group, Div,Button, Avatar, PanelHeader,HeaderButton, platform, IOS } from '@vkontakte/vkui';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';

const osname = platform();

const Balance = ({ id, go, fetchedUser }) => (
	<Panel id={id}>
	<PanelHeader
			left={<HeaderButton onClick={go} data-to="home">
				{osname === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			VK Play (βeta)
		</PanelHeader>

		{fetchedUser &&
		<Group title="Пользователь">
			<ListItem
				before={fetchedUser.photo_200 ? <Avatar src={fetchedUser.photo_200}/> : null}
				description={fetchedUser.city && fetchedUser.city.title ? fetchedUser.city.title : ''}
			>
				{`${fetchedUser.first_name} ${fetchedUser.last_name}`}
			</ListItem>
        </Group>}
        
		<Group title="Баланс VK Coim">
			<Div>
				<center>В разработке</center>
			
                  </Div>
		
		</Group>

        <Group title="Баланс VKP">
			<Div>
				<center>Баланс: 0</center>
				<Div style={{display: 'flex'}}> 
                     <Button size="l" stretched style={{ marginRight: 8 }}>Пополнить</Button> 
                     <Button size="l" stretched level="secondary">Вывести</Button> 
                  </Div>
			</Div>
		</Group>
        </Panel>
);	

export default Balance;